<template>
  <v-list subheader>
    <v-list-item  @click="redirectTo(item.redirectTo)" class="item-signOut" v-for="(item, index) in ListSignOut" :key="index"  style="text-decoration: none; ">

            <v-list-item-icon >
                <v-icon v-text="item.icon"></v-icon>
            </v-list-item-icon>
            <v-list-item-content>
                <v-list-item-title v-text="item.name"></v-list-item-title>
            </v-list-item-content>

    </v-list-item>
  </v-list>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  data: () => ({}),
  computed: {
    ...mapState(["ListSignOut"])
  },
  methods: {
      redirectTo(url){
        console.log(url);
        if(url === '/app1/login'){
          localStorage.removeItem('iToken')
          /* Mode dev */  
          /* return location.reload(); */

         /* mode github pages */
        /*  return window.location.href = 'https://lmelgarejo96.github.io/castleoftalents/index'; */
        }
        this.$router.push(url);
      }
  }
};
</script>

<style>

.item-signOut:hover {
    background: rgba(0,0,0, .6);
    color: #fff;
}

.item-signOut:hover .v-list-item__icon i,  .item-signOut:hover .v-list-item__content .v-list-item__title {
    color: #fff;
}
</style>