<template>
  <div>
    <div class="login-container" v-if="!charging">
      <div class="title-return">
        <a @click="returnHome">
          <i class="material-icons text-white">arrow_back</i>
          <p>GO HOME</p>
        </a>
      </div>

      <div class="section-img">
        <img class="gif-bg" src="../../assets/img/iglesia.gif" />
        <img class="img-bg" src="../../assets/img/kids2.png" alt="kids" />
        <img class="img-bg-logo" src="../../assets/img/logo.jpg" alt="logo" />
      </div>
      <div class="login-form">
        <div class="container">
          <div id="card">
            
            <div class="text-center text-white mb-3 title-lion">
              <!-- <img id="logo-cdt"  src="../../assets/img/logo.jpg" /> -->
              <h4>Sign Up</h4> 
              <img src="../../assets/img/lion.png" alt="kids" />
            </div>

            <div v-if="errors || errors.length > 0">
                <div  v-for="(error, index) in errors" :key="index"  class="alert alert-danger alert-dismissible fade show" role="alert" style="margin: .8em;">
                    <strong> {{ error.name }} </strong>
                    <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
            </div>
            

            <v-form v-on:submit.prevent="Ingresar">
              <v-col cols="12">
                <v-text-field
                  dark
                  outlined
                  id="username-cdt"
                  color="#fff"
                  v-model="email"
                  label="Usuario"
                  required
                ></v-text-field>
                <v-text-field
                  dark
                  id="password-cdt"
                  outlined
                  color="#fff"
                  v-model="password"
                  :append-icon="show1 ? 'visibility' : 'visibility_off'"
                  :type="show1 ? 'text' : 'password'"
                  name="input-10-1"
                  label="Contrasea"
                  hint="At least 8 characters"
                  @click:append="show1 = !show1"
                  counter
                ></v-text-field>
                <div class="text-center" style="margin-top: 10px;">
                  <button
                    id="btn-cdt"
                    @submit="Ingresar($event)"
                    class="btn btn-info btn-block"
                  >Ingresar</button>
                </div>
                <br />
              </v-col>
            </v-form>
          </div>
        </div>
      </div>  
    </div>

    <Loader v-else />
  </div>
</template>

<script>
import Loader from "../Loader";
export default {
  components: {
    Loader
  },
  data: () => ({
    email: "",
    password: "",
    show1: false,
    charging: false,
    errors: []
  }),
  mounted() {
    console.log(this.$router);
  },
  methods: {
    enterLogin() {},
    Ingresar() {
        this.charging = true;
        /* setTimeout(() => {
            this.errors = [];
            this.charging = false;
            this.password = '';
            this.errors.push({name: 'Invalid Username!!'}, {name: 'Invalid Password!!'});
        }, 2000); */
        setTimeout(() => {
            if( this.email === 'admin' && this.password ==="123456"){
              localStorage.setItem("iToken", this.email + this.password);
              this.$router.push("/app1/intranet/");
            }else if(this.email === 'admin' && this.password !=="123456"){
              this.errors = [];
              this.charging = false;
              this.password = '';
              this.errors.push({name: 'Invalid Password!!  try with "123456" password '});
            }else {
              this.errors = [];
              this.charging = false;
              this.password = '';
              this.errors.push({name: 'Invalid Username!! try with "admin" username'}, {name: 'Invalid Password!!  try with "123456" password '});
            }
        },2000)
    },
    returnHome() {
      this.$router.push("/");
    }
  }
};
</script>

<style>
.login-container {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  height: 100vh;
}

.section-img {
  width: 70%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #b993d6; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #8ca6db,
    #b993d6
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #8ca6db,
    #b993d6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.img-bg {
  width: 35%;
  z-index: 50;
  position: absolute;
  bottom: 5px;
  animation: ease-in animatedUp 2s;
}

.gif-bg {
  position: absolute;
  height: 100%;
  width: 80%;
  animation: ease-in animatedDown 1.5s;
}

.img-bg-logo {
  width: 7%;
  z-index: 50;
  position: absolute;
  bottom: 12%;
  animation: ease-in animatedUp 2s;
}

.title-lion {
  display: flex;
  flex-flow: column-reverse nowrap;
  justify-content: center; 
  align-items: center;
}

.title-return {
  margin: 1.8em;
  z-index: 60;
  position: absolute;
  top: 0;
  left: 0;
  animation: ease-in animatedLeftToRight 2s;
}

.title-return a {
  text-decoration: none;
  color: #fff;
  /* padding: 2em; */
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

/* .title-return > a:hover > i:hover, .title-return a:hover p{
    color: #17a2b8;
} */

.title-return a p {
  color: #fff;
  margin-left: 10px;
}

.login-form {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  width: 30%;
  height: 100%;
  background: #41295a; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #2f0743,
    #41295a
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2f0743,
    #41295a
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#card {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: space-around;
  animation: ease-in animate 3s;
  background-color: #fff;
  padding-top: 2em;
  background: #41295a; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #2f0743,
    #41295a
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2f0743,
    #41295a
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#card img {
  width: 120px;
  border-radius: 10px;
}

/* #btn-cdt {
  background: #fff;
  color: #000;
} */

/* #btn-cdt:hover {
  background: #17a2b8;
  border: 1px solid #169eb3;
  color: #fff;
} */

@keyframes animate {
  0% {
    opacity: 0;
    transform: translateX(800px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes animatedDown {
  0% {
    opacity: 0;
    transform: translateY(-800px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes animatedUp {
  0% {
    opacity: 0;
    transform: translateY(800px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes animatedLeftToRight {
  0% {
    opacity: 0;
    transform: translateX(-800px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@media screen and (max-width: 1000px) {
  .login-container {
    background: #00b4db; /* fallback for old browsers */
    background: -webkit-linear-gradient(
      to right,
      #0083b0,
      #00b4db
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
      to right,
      #0083b0,
      #00b4db
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  }
  .section-img {
    display: none;
  }
  .login-form {
    width: 100%;
  }
  #card {
    padding: 4em;
    width: 100%;
    -webkit-box-shadow: 10px 10px 5px 4px rgba (0, 0, 0, 0.75);
    -moz-box-shadow: 10px 10px 5px 4px rgba (0, 0, 0, 0.75);
    box-shadow: 10px 10px 5px 4px rgba (0, 0, 0, 0.75);
    animation: ease-in animatedUp 1s;
  }

}

@media screen and (max-width: 450px) {
  #card {
    padding: 0;
    padding-top: 2em;
  }
}
</style>