import Vue from 'vue'
import VueRouter from 'vue-router'
import Intranet from '../views/Intranet/Intranet.vue';
import LoginI from '../components/IntranetComponents/Login';

console.log('router');
Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    redirect: '/app1/intranet'
  },
  {
    path: '/app1/login',
    name: 'login-intranet',
    component: LoginI
  },
  {
    path: '/',
    name: 'login-intranet',
    component: LoginI
  },
  {
    path: '/app1/intranet',
    name: 'intranet',
    component: Intranet,
    children: [
      {
        path: '/app1/intranet/',
        name: 'home-intranet',
        component: () => import('../components/IntranetComponents/Home'),
      },
      {
        path: '/app1/intranet/alumnos',
        name: 'alumnos-intranet',
        component: () => import('../components/IntranetComponents/Alumnos.vue'),
      },
      {
        path: '/app1/intranet/docentes',
        name: 'docentes-intranet',
        component: () => import('../components/IntranetComponents/Docentes'),
      },
      {
        path: '/app1/intranet/cursos',
        name: 'cursos-intranet',
        component: () => import('../components/IntranetComponents/Cursos'),
      },

      {
        path: '/app1/intranet/myaccount',
        name: 'alumnos-intranet',
        component: () => import('../components/IntranetComponents/Account.vue'),
      },
      
    ],
    meta: {
      autenticate: true
    } 
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

/* router.options.routes[2].path = 'asdfdsfsdf'
router.options.routes.splice(2, 1);
console.log('router',router); */

router.beforeEach((to, from, next) => {
  let iToken = localStorage.getItem('iToken');
  let autorizacion = to.matched.some(record => record.meta.autenticate)
  if(iToken == null && autorizacion){
    next('/app1/login')
  }else if(iToken!=null && !autorizacion){
    next('/app1/intranet')
  }else{
    next()
  }
}) 

export default router
