<template>

    <div class="container-fluid">
      <v-list-item id="item-avatar" class="mb-2">
        <v-list-item-avatar>
          <v-img :src="user.image"></v-img>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>{{ user.name }} {{ user.lastName }}</v-list-item-title>
          <v-list-item-subtitle>{{ user.username }}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>

      <!-- <v-divider></v-divider> -->

      <v-list  nav dense style=" border-radius: 10px; ">
        <v-list-item-group v-model="lista"  :mandatory="true">
          <v-list-item color="#2980b9" @click="redirectTo(item.url)" v-for="item in navigations" :key="item.title" link  >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>

      <Anounces  id="anounce-list"/>

    </div>

</template>

<script>
import { mapState, mapActions } from "vuex";
import Anounces from './Anounces';

export default {
  components: {
    Anounces
  },
  props: {
      toolbar: Boolean
  },
  data: () => ({
    lista: 0
  }),
  computed: {
    ...mapState(["user", 'navigations'])
  },
  mounted(){
    this.updateList()
  },
  methods: {
    redirectTo( url ) {
      this.$router.push(url);
    },
    updateList() {
      for(let i=0; i < this.navigations.length; i++){
        if( window.location.pathname === this.navigations[i].url ){
          return this.lista = i
        }
      }
    }
  }
};
</script>

<style>
#item-avatar {
  background: rgba(47, 31, 66, 0.2);
  border: 1px solid rgba(235, 217, 217, 0.9);
  border-radius: 10px;
}
#item-avatar .v-list-item__content .v-list-item__title, #item-avatar .v-list-item__content .v-list-item__subtitle{
  color: #fafafa;
}

@media screen and (max-width: 900px) {
    #anounce-list {
        display: none;
    }
}
</style>