<template>
  <div >
      <v-list class="mt-5" nav dense style=" border-radius: 10px; padding: 0; ">
        <v-list-item-group v-model="anounce" style="margin: 0; padding: 0;  background: #e3e5eb;">
          <v-list-item @click="redirectToEvent(item.id)"  class="mt-5" v-for="item in anounces" :key="item.name" link  style="margin: 0; padding: 0; background: #e3e5eb;">
            <v-img  :src="item.image" :alt="item.name" style="border-radius: 10px;"></v-img>
          </v-list-item>
        </v-list-item-group>
      </v-list>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
export default {
    data: () => ({
        anounce: 0
    }),
    computed: {
        ...mapState(['anounces'])
    },
    methods: {
        redirectToEvent(id){
            this.$router.push('/app1/intranet/events/'+id)
        }
    }
}
</script>

